import {  
    mapMutations  
} from 'vuex';

export default{
	data(){
		return {
			cuowu:'',
			cnnumber:'',
			cnpwd:'',
			logining: false,
			showPassword: true,
			realname:'',
			token:'',
			keyong:0,
			checked:false,
			callback:''
		}
	},
	onLoad(e) {
		if(e.callback){
			this.callback = e.callback;
		}
	},
	onShow() {
		var token = uni.getStorageSync('cbe_token');
		if(token){
			this.token = token;
		}else{
			uni.showToast({ title:"未登录或无权限",icon: "none"});
			var timer = setTimeout(()=>{
				uni.navigateBack({
					delta:1
				})
			},1000);
		}
	},
	methods: {
		...mapMutations(['bindCn','login']),
		inputChange(e){
			this.cuowu = '';
			this.keyong = 0;
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		changePassword() {
			this.showPassword = !this.showPassword;
		},
		changexy:function(){
			this.checked = !this.checked;
		},
		quxiao(){
			if(this.callback){
				uni.navigateBack({
					delta:1
				})
			}else{
				uni.switchTab({
					url:"../user/user"
				})
			}
		},
		inputChangm(e){
			this.cuowu = '';
			this.realname = '';
			this.cnpwd = '';
			var cnnumber = e.detail.value;
			this.cnnumber = cnnumber; //CN0600610
			if(cnnumber.length<=8){
				this.keyong = 0;
			}else{
				uni.request({
					url:uni.getStorageSync('hostname') + '?action=index/login/checkbdcn',
					data: {cnnumber:cnnumber},
					method:'POST',
					dataType:'json',
					header: {'content-type' : 'application/x-www-form-urlencoded'},
					success: (resa) => {
						var one = resa.data;
						if(one.code==0){
							this.keyong = 1;
						}else{
							this.keyong = 2;
						}
					}
				})
			}
		},
		checkcn(){
			if(!this.cnnumber){
				return false;
			}
			if(!this.cnpwd){
				return false;
			}
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/login/checkcn',
				data: {cnnumber:this.cnnumber,cnpwd:this.cnpwd},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					var one = resa.data;
					if(one.code==0){
						this.cuowu = '';
						this.realname = one.realname;
					}else{
						this.cuowu = one.msg;
					}
				}
			})
		},
		toNext(){
			var that = this;
			this.logining = true;
			if(!this.cnnumber){
				uni.showToast({ title: '请填写CN号', icon: 'none' });
				this.logining = false;return false;
			}
			if(!this.cnpwd){
				uni.showToast({ title: '请填写CN号密码', icon: 'none' });
				this.logining = false;return false;
			}
			if(!this.checked){
				uni.showToast({ title: '绑定CN号需同意《用户协议》', icon: 'none' });
				this.logining = false;return false;
			}
			uni.showLoading({
				title: '正在绑定',
				mask: true
			});
			uni.request({
				url:uni.getStorageSync('hostname') + '?action=index/login/bindcn',
				data: {cnnumber:this.cnnumber,cnpwd:this.cnpwd,token:this.token},
				method:'POST',
				dataType:'json',
				header: {'content-type' : 'application/x-www-form-urlencoded'},
				success: (resa) => {
					uni.hideLoading();
					var one = resa.data;
					if(one.code==0){
						this.cuowu = '';
						uni.showToast({
							title:one.msg,icon:'success',mask:true,duration: 3000
						});
						if(that.callback){
							var timer = setTimeout(()=>{
								uni.navigateBack({
									delta:1
								})
							},2000);
						}else{
							this.login(one.data);
							var timer = setTimeout(()=>{
								uni.switchTab({
									url:"../user/user"
								})
							},2000);
						}
					}else{
						this.cuowu = one.msg;
						this.logining = false;
					}
				}
			})
		}
	}
}
